      var tokensToBuy = BigNumber(this.tokensToBuy);
      var decimals = BigNumber(this.currentItem.Decimals);
      var tokensAndBonus = BigNumber(this.tokensAndBonus);

      if (decimals.isNaN()) return null;
      // console.log("Decimals: ", decimals.sd());

      var maxValue = freeTokens.shiftedBy(-decimals.toNumber());

      // console.log("MaX:", maxValue.toFormat());

      var step = this.tokensStep(this.currentItem.Price, this.currentItem.Decimals);

      var numberofStep = tokensToBuy.div(step).dp(0, 1);

      if (numberofStep === null) return true;

